html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  /* font-weight: 200; */
  /* color: #3e5869; */
}

#root {
  height: 100%;
}

.u-background-gray {
  background-color: #F6F6F6;
}

.u-background-blue {
  background-color: #1887FB;
}

.u-text-white {
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* REACT-CHAT-APP */

.app {
  display: grid;
  height: 100vh;
  grid-template-columns: minmax(250px, auto) repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr) 60px;
  grid-template-areas:
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "n f f f f f";
}

.new-room-form {
  grid-area: n;
}

.rooms-list {
  grid-area: r;
}

.message-list {
  grid-area: m;
}

.send-message-form {
  grid-area: f;
}

/* REST OF CSS */
.rooms-list {
  box-sizing: border-box;
  padding: 10px;
  background-color: var(--main-color);
  overflow: auto;
  height: 100%;
}

.rooms-list>ul {
  list-style-type: none;
  padding: 0;
  overflow: scoll;
}

.rooms-list li {
  margin: 10px 0;
}

.rooms-list>h3 {
  font-size: 1.5em;
  margin: 5px 0;
  color: var(--main-text-color);
}

.rooms-list .room div {
  color: var(--secondary-text-color);
  /* font-weight: 600; */
  text-decoration: none;
  cursor: pointer;
  padding: 0.5em;
}

.rooms-list .room.active div {
  color: var(--main-text-color);
  font-weight: 600;
}

.new-room-form {
  padding: 0 0.5em;
  background: var(--background-color);
  color: var(--primary-message-color);
}

.new-room-form form {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.new-room-form input {
  width: 135px;
  background: var(--background-color);
  font-size: 0.8em;
}

.new-room-form button {
  background: var(--background-color);
  color: var(--primary-message-color);
  border: 0;
}

.new-room-form input::-webkit-input-placeholder {
  color: var(--primary-message-color);
  font-weight: 300;
}

.new-room-form input::-ms-input-placeholder {
  color: var(--primary-message-color);
  font-weight: 300;
}

.new-room-form input::placeholder {
  color: var(--primary-message-color);
  font-weight: 300;
}

.new-room-form input:focus {
  outline-width: 0;
}

.new-room-form input {
  border: 0;
}

.new-room-form button {
  border: 0;
}

.message-list {
  box-sizing: border-box;
  padding-left: 6px;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: var(--background-color);
}

.message-list .join-room {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 34px;
  font-weight: 300;
}

.send-message-form {
  background: var(--send-message-form);
  display: flex;
}

.send-message-form input {
  width: 100%;
  padding: 15px 10px;
  margin: 0;
  border-style: none;
  background: var(--send-message-form);
  font-weight: 300;
  font-size: 1em;
}

.send-message-form input:focus {
  outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
  color: var(--primary-message-color);
}

.send-message-form input::-ms-input-placeholder {
  color: var(--primary-message-color);
}

.send-message-form input::placeholder {
  color: var(--primary-message-color);
}

.help-text {
  position: absolute;
  top: 10px;
}
.message-container {
  display: flex;
  justify-content: flex-start;
}

.message-avatar {
  padding: 0.5em 0.1em;
}

.message {
  padding: 1em 0.5em;
}

.message .message-username {
  font-size: 0.9em;
  color: var(--primary-message-color);
  /* opacity: 0.9; */
  margin-bottom: 6px;
  font-weight: 500;
}

.message .message-text {
  /* background: var(--main-color); */
  /* color: var(--primary-message-color); */
  font-size: 0.9em;
  display: inline-block;
  padding: 0.6em 0.7em;
  border-radius: 1.5em;
  font-weight: 300;
  line-height: 1.5em;
  /* border-bottom-left-radius: 2em; */
}

.message.u-others-messages {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.register-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.register-form {
  display: flex;
  flex-direction: column;
  height: 12em;
  justify-content: space-around;
}

.register-form input {
  /* min-height: 1em;
  font-size: 1em;
  padding: 1em 0.5em; */

  border: 1px solid #ddd;
  border-radius: 3px;
  /* height: 44px; */
  padding: .75em 1em;
  /* width: 100%; */
  -webkit-transition: all .1s;
  transition: all .1s;
  font: inherit;
}

.register-button {
  height: 3em;
  padding: .75em 1em;
  width: 100%;
  background-color: #145CBE;
  color: white;
  -webkit-transition: all .1s;
  transition: all .1s;
  font: inherit;
}
